@font-face {
    font-family: 'Roboto';
    src: url('./fonts/Roboto-Regular.ttf') format('truetype');
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    max-width: 960px;
    margin: 1rem auto;
}

h1 {
    text-align: center;
    margin-bottom: 1rem;
}

ul {
    list-style: none;
    
    li {
        padding: 1rem 2rem;
        border: 1px solid #ccc;
        border-radius: 10px;
        margin-bottom: 1rem;
    }
}

button {
    padding: 1rem 1.5rem;
    border: none;
}

body {
    background: url('./assets/winter-bg.jpg') no-repeat;
    background-size: auto;
    // background-position: center;
}

.weather-block {
    display: flex;
    justify-content: space-between;
    max-width: 500px;
    margin: 0 auto;

    .weather-block-item {

        width: 150px;
        height: 150px;
        border-radius: 10px;
        background-color: red;
    }
}